
.page-title {
  font-size: 24px;
  margin-top: 10px;
  margin-bottom: 10px;
  font-weight: 900;
  font-family: "Georgia", "serif";
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 10px;
}
#tooltip {
  position: absolute;
  z-index: 999;
  color: white;
  font-size: 15px;
  width: 200px;
  height: 200px;
}
.line-chart {
  position: relative;
  display: inline-block;
  width: 1000px;
  height: 300px;
  border: 1px solid #bcbcbc;
}
.pie-chart {
  position: relative;
  display: inline-block;
  width: 498px;
  height: 300px;
  border: 1px solid #bcbcbc;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

.basic-button {
  width: 80px;
  height: 25px;
  margin-top: 10px;
  margin-bottom: 10px;
}
